import * as types from '../mutation-types.js'
import {
	defaultNav
} from '@/default/default.js'
import { defaultStyle } from '../../default/default.js'
export default{
	namespaced:true,
	state:{
		backgroundImage:'',//背景图片
		opacity:0//网站透明度
	},
	mutations:{
		[types.CHANGE_MAIN_BACKGROUNDIMAGE](state,{
			url
		}){
			state.backgroundImage=url
			localStorage.v_backgroundImage=url
		},
		[types.CHANGE_MAIN_OPACITY](state,val){
			state.opacity=val
			localStorage.v_opacity=val
		},
		[types.INIT_MAIN_STYLE](state){
			localStorage.v_backgroundImage=localStorage.v_backgroundImage?localStorage.v_backgroundImage:defaultStyle.backgroundImage
			localStorage.v_opacity=localStorage.v_opacity?localStorage.v_opacity:defaultStyle.opacity
			state.backgroundImage=localStorage.v_backgroundImage
			state.opacity=localStorage.v_opacity
		}
	}
}