<template>
  <q-layout view="lHh LpR lFf">
    <q-header elevated v-model="header">
      <q-toolbar>
        <q-btn icon="menu" flat @click="drawerLeft = !drawerLeft"></q-btn>
        <q-toolbar-title> Quasar Navigation Version 1.0.0 </q-toolbar-title>
      </q-toolbar>
    </q-header>
    <q-drawer
      elevated
      v-model="drawerLeft"
      :width="200"
      :breakpoint="700"
      elevated
      content-class="bg-primary text-white"
    >
      <q-scroll-area class="fit">
        <q-btn class="fit" flat color="primary" text-color="info">
          <q-item class="fit">
            <q-item-section> 透明度 </q-item-section>
            <q-item-section>
              <q-slider
                v-model="opacity"
                :min="0"
                :max="1"
                label
                :step="0.1"
                color="light-blue"
                @change="
                  (val) => {
                    CHANGE_MAIN_OPACITY(val);
                  }
                "
              />
            </q-item-section>
          </q-item>
        </q-btn>
        <q-btn
          padding="13px"
          class="fit"
          flat
          color="primary"
          text-color="info"
          icon="add"
          label="添加新的导航"
          @click="addNewNavCard = true"
        />
        <q-btn
          padding="13px"
          class="fit"
          flat
          color="primary"
          text-color="info"
          icon="add"
          label="添加背景图"
          @click="newBackground"
        />
        <q-btn
          padding="13px"
          class="fit"
          flat
          color="primary"
          text-color="info"
          icon="sort"
          label="设置卡片顺序"
          @click="handleReorder"
        />
      </q-scroll-area>
    </q-drawer>
    <q-page-container>
      <q-page>
        <router-view></router-view>
      </q-page>
    </q-page-container>

    <q-dialog v-model="addNewNavCard" persistent>
      <!-- 添加新的导航 -->

      <q-card class="my-card" style="width: 350px">
        <q-form @submit="confirmAddNewWebsite">
          <q-toolbar>
            <q-btn flat round dense icon="add" class="q-mr-sm" />
            <q-toolbar-title>添加新的导航</q-toolbar-title>
          </q-toolbar>
          <q-card-section horizontal>
            <q-card-section width="50%">
              <q-input
                v-model="newWebName"
                label="网站名称"
                :rules="rules.empty"
              />
            </q-card-section>
            <q-card-section>
              <q-input
                v-model="newWebUrl"
                label="网站URL"
                :rules="rules.empty"
              ></q-input>
            </q-card-section>
          </q-card-section>
          <q-card-section>
            <q-input v-model="newWebBackgroundColor" label="网站背景颜色">
              <template v-slot:append>
                <q-icon name="colorize" class="cursor-pointer">
                  <q-popup-proxy>
                    <q-color v-model="newWebBackgroundColor"></q-color>
                  </q-popup-proxy>
                </q-icon>
              </template>
            </q-input>
          </q-card-section>
          <q-separator />
          <q-card-actions align="right">
            <q-btn v-close-popup flat color="primary" label="取消" />
            <q-btn flat color="primary" round icon="check" type="submit" />
          </q-card-actions>
        </q-form>
      </q-card>
    </q-dialog>

    <q-page-sticky
      position="top-right"
      :offset="[18, 18]"
      @click="
        header = !header;
        drawerLeft = !drawerLeft;
      "
    >
      <q-btn round color="accent" icon="menu" class="rotate-45" />
    </q-page-sticky>
  </q-layout>
</template>

<script>
import bus from '../util/eventBus'
import { mapMutations } from "vuex";
export default {
  name: "Layout",
  data() {
    return {
      drawerLeft: false,
      header: false,
      addNewNavCard: false,
      卡片排序_对话框:false,
      newWebName: "", //新的网站名称
      newWebUrl: "", //新的网站链接
      newWebBackgroundColor: "",
      opacity: Number(localStorage.v_opacity), //网站透明值
      rules: {
        empty: [(v) => !!v || "请不要空着"],
      },
    };
  },
  methods: {
    ...mapMutations("moduleNav", ["addNewNav"]), //获得操作添加新项目
    ...mapMutations("moduleStyle", [
      "CHANGE_MAIN_BACKGROUNDIMAGE",
      "CHANGE_MAIN_OPACITY",
    ]), //改变新的背景url
    confirmAddNewWebsite() {
      this.addNewNav({
        name: this.newWebName,
        url: this.newWebUrl,
        backgroundColor: this.newWebBackgroundColor,
      });
      this.addNewNavCard = false;
    },
    newBackground() {
      this.$q
        .dialog({
          title: "新的背景图",
          message: "请输入url",
          cancel: true,
          prompt: {
            model: "",
            type: "text",
          },
        })
        .onOk((res) => {
          this.CHANGE_MAIN_BACKGROUNDIMAGE({
            url: res,
          });
        });
    },
    handleReorder(){
      // 显示卡片排序
      this.bus.$emit("显示_卡片排序")
    }
  },
};
</script>

<style scoped="scoped" lang="stylus">
@import '../assets/style/color.styl';
</style>
