<template>
	<div id="app">
		<router-view />
	</div>
</template>

<script>
	import {mapMutations} from 'vuex'
	export default {
		name: 'App',
		methods:{
			...mapMutations('moduleNav',['initNav']),
			...mapMutations('moduleStyle',['INIT_MAIN_STYLE'])
		},
		mounted() {
			this.initNav()
			this.INIT_MAIN_STYLE()//初始化主页样式
		}
	}
</script>

<style lang="stylus">
	*::-webkit-scrollbar {
		display: none
	}
	body,
	html {
		height: 100%;
	}
	#app {
		height: 100%;

	}
</style>
