<template>
	<div class="home" :style="style">
		<Search :opacity="opacity"></Search>
		<navCard :opacity="opacity"></navCard>
		<Reorder ></Reorder>
	</div>
</template>

<script>
	import Search from './components/Search.vue'
	import navCard from './components/navCard.vue'
	import Reorder from './components/Reorder.vue'
	import {mapState} from 'vuex'
	export default {
		name: 'Home',
		components: {
			Search,
			navCard,
			Reorder
		},
		data() {
			return {
				style: {
					'background-image': ''
				}
			}
		},
		computed: {
			...mapState('moduleStyle',{
				backgroundImage: state => state.backgroundImage,
				opacity:state=>state.opacity
			})
		},
		watch:{
			backgroundImage(){
				this.init()
			}
		},
		mounted() {
			this.init()
		},
		methods:{
			init(){
				this.style["background-image"]='url('+this.backgroundImage+')'
			}
		}
	}
</script>
<style scoped="scoped">
	.home {
		padding: 0 14%;
		background-origin: border-box;
		background-attachment:fixed;
		min-height: 100vh;
	}
</style>
